body {
  margin: 0;
  padding: 0;
  font-family: 'Mitr', sans-serif;
  color: #fff;
  letter-spacing: 2px;
  font-size: 1.1em;

  position: relative;
  margin: 0;
  padding-bottom: 6rem;
  min-height: 100%;

  background-color: black;

  background-image: radial-gradient(circle contain, rgba(150, 111, 195, 0.2) 180px, rgba(150, 111, 195, 0.3) 190px, transparent 200px),
