      radial-gradient(circle contain, rgba(224, 124, 203, 0.2) 80px, rgba(224, 124, 203, 0.3) 17px, transparent 20px),

      radial-gradient(circle contain, rgba(145, 75, 113, 0.2) 190px, rgba(145, 75, 113, 0.3) 41px, transparent 180px),
      radial-gradient(circle contain, rgba(145, 75, 113, 0) 100px, rgba(145, 75, 113, 0.2) 190px, transparent 80px),        
      radial-gradient(circle contain, rgba(145, 75, 113, 0) 150px, rgba(145, 75, 113, 0.2) 60px, transparent 450px),        

      radial-gradient(circle contain, rgba(37, 85, 79, 0.2) 150px, rgba(37, 85, 79, 0.3) 34px, transparent 150px),
