  background-size: 1500px 750px;
  background-position: 600px 150px, 2000px -350px, -540px -250px, 250px -250px, -150px 50px, 500px -50px, -250px -300px, -300px -100px, -400px -400px, -800px -150px, -100px -50px, 650px -300px, 490px -200px, 900px 0px;   
